
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.fade-leave {
  /* opacity: 1; */
  /* No reason to set opacity. It is 1 by default */
}
.fade-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
  opacity: 0;
}
